"use client"; // 애니메이션 쓰려면 필수

import Link from "next/link";
import Logo from "@/components/ui/Logo";
import { motion } from "framer-motion"; // npm install framer-motion 필요

// 메타데이터는 클라이언트 컴포넌트에서 분리해야 하지만, 일단 빠른 확인을 위해 주석 처리하거나 layout으로 이동 권장
// export const metadata = { ... }; 

export default function AboutPage() {
  return (
    <main className="min-h-screen bg-[#FDFBF9] flex items-center justify-center">
      <motion.div 
        initial={{ opacity: 0, y: 30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8, ease: "easeOut" }}
        className="mx-auto max-w-screen-xl px-8 py-32 flex flex-col items-center text-center"
      >
        {/* 로고: 너무 작지 않게 키움 */}
        <div className="mb-16">
             <Logo href="/" className="h-8 w-auto text-black" />
        </div>

        {/* 타이틀: 크고, 자간 좁게, 명조체 느낌(Serif) 혹은 아주 굵은 고딕 */}
        <h1 className="font-serif text-5xl md:text-6xl font-medium tracking-tight text-[#111111] mb-8 leading-tight">
          Brand Story
        </h1>

        {/* 구분선: 매트 오렌지 포인트 */}
        <div className="w-12 h-1 bg-[#D85C27] mb-10"></div>

        {/* 본문 1: 가독성 좋은 회색, 자간 좁게, 줄간격 넓게 */}
        <p className="font-sans text-lg md:text-xl text-[#4B5563] tracking-tight leading-relaxed max-w-2xl break-keep">
          오마쥬 의원은 공장형 시술을 지양하고,<br/>
          오직 당신만을 위한 <span className="text-[#D85C27] font-semibold">프라이빗한 의학적 큐레이션</span>을 제공합니다.
        </p>

        {/* 본문 2: 감성적인 문구 */}
        <p className="mt-6 font-serif text-xl md:text-2xl text-[#222] tracking-tight italic">
          "본연의 아름다움을 깨우는 Atelier. The Private Masterpiece."
        </p>

        {/* 홈 버튼: 고급스러운 버튼 스타일 */}
        <Link
          href="/"
          className="mt-16 px-8 py-3 rounded-full border border-[#D85C27] text-[#D85C27] 
                     font-medium hover:bg-[#D85C27] hover:text-white transition-all duration-300
                     tracking-tight text-sm uppercase"
        >
          Back to Home
        </Link>
      </motion.div>
    </main>
  );
}